import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Orders from '@/components/Orders'
import Products from '@/components/Products'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/Orders',
      name: 'Orders',
      component: Orders
    },
    {
      path: '/Products',
      name: 'Products',
      component: Products
    }
  ]
})



